
.box{
    width: 30px;
    height: 30px;
    float: left;
    transition: all .2s;
    margin: 5px;
}
.boxselected{
    float: left;
    transition: all .2s;
    margin: 5px;
    width: 30px;
    height: 30px;
    box-shadow: 0px 5px 5px;
}
.box:hover{
    box-shadow: 0px 5px 5px;
    cursor: pointer;

}

